.small-hero{
    padding: clamp(3.75rem, 3.2437rem + 2.5316vw, 6.25rem) 0;
    width: 100%;
    background-color: var(--primary-color);
}

.small-hero__wrapper{
    position: relative;
    display: flex;
    flex-direction: column;
    width: 100%;
}

.small-hero__bg{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 90%;
    display: flex;
    align-items: center;
    z-index: 0;
    color: var(--secondary-color);
    font-size: clamp(4.5rem, 2.981rem + 7.5949vw, 11.3rem);
    font-weight: 900;
    opacity: 0.4;
    padding: 0 var(--space-1);

}

.small-hero__heading,
.small-hero__text{
    width: 70%;
    color: var(--quaternary-color);
    z-index: 1;
}

.small-hero__heading{
    text-transform: uppercase;
}

@media screen and (max-width: 700px) {
    .small-hero__text{
        width: 100%;
    }
}